<template>
	<div>
		<!--<v-list class="icon-button" v-on:abc="fc" v-bind:items="items">
			<img height="10" width="10" slot="title" src="../assets/logo.png" />
		</v-list>-->
	
		<!--<v-list v-bind:msg="msg" class="icon-button" v-on:abc="fc2">
		<img height="30" width="30" slot="icon" src="../assets/logo.png" />
	    </v-list>-->
	    
	    <button @click="doCheckView">切換組件</button>
	    <button @click="doCheckView2">切換組件2</button>
	    
	    <component :is="currentView" class="icon-button" v-on:abc="fc" v-bind:items="items" btnName="外部傳入" :text="newText" keep-live>
	    	<img slot="icon" src="../assets/logo.png">
	    	<span slot="title">鑫</span>
	    	<span slot="requer">*</span>
	    </component>
	    
	</div>
</template>

<script>
	import List from '@/components/list'
	import Btn from '@/components/btn'
	import ImgInput from '@/components/ImgInput'
	export default{
		data(){
			return{
				msg:' 下載',
				items:[{id:1,name:'a'},{id:2,name:'b'}],
				currentView:'v-list',
				newText:'123123'
				}
		},
		components:{
			'v-list':List,
			'v-btn':Btn,
			'v-imgbut':ImgInput
		},
		methods:{
			fc:function(item){
				alert(item.name);
			},
			
			doCheckView:function(){
				this.currentView=this.currentView=='v-list'?'v-btn':'v-list';
			},
			doCheckView2:function(){
				this.currentView="v-imgbut";
			}
		}
	}
</script>

<style>
	.icon-button{
		
		font-size: 12px;
		line-height: 30px;
		width:400px;
		background-color: gold;
	}
</style>


