<template>
	<!--<div v-on:click="alt">
		 <slot name="icon"></slot><span>{{msg}}</span>
	</div>
	-->
	
	<div>
		<slot name="title"></slot>
		<ul v-for="(item,index) in items" :key="itemKey" >
			<li @click="alt(item)">{{item.name}}</li>
		</ul>
	</div>
	
</template>

<script>
	export default{
		props:['items','itemKey'],
		methods:{
			alt:function(item){
				//alert('mmmmm');
				this.$emit('abc',item);
			}
		}
	}
</script>

