import Vue from 'vue'
import Router from 'vue-router'

import Index from '@/components/index'
import A from '@/components/A'
import B from '@/components/B'
import C from '@/components/C'

Vue.use(Router)

export default new Router({
	mode:'history',
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index,
      children:[
      					{
      						path:'/a',
      						name:'A',
      						component:A
      					},
      					{
      						path:'/b',
      						name:'B',
      						component:B
      					},
      					{
      						path:'/c',
      						name:'C',
      						component:C
      					},
      ]
    }
  ]
})



