<template>
	<div><slot name="icon"></slot> {{btnName}}
	<input type="text" v-model="getText"/>
	</div>
</template>

<script>
	export default{		
		props:['btnName','text'],
		computed:{			
			getText:function(){				
				return {msg: this.text};				
			}
		}
	}
</script>

<style>
	
</style>


